@import url(https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@300;400;500;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Gemunu Libre', sans-serif;
}

.nav-disable-animation {
  -webkit-animation: 2s move-menu-to-right-close;
          animation: 2s move-menu-to-right-close;
}

.nav-active-animation {
  -webkit-animation: 2s move-menu-to-right-open;
          animation: 2s move-menu-to-right-open;
}

