 */
@-webkit-keyframes move-menu-to-right-open {
  0% {
    width: 0%;
    left: -100%;
  }
  100% {
    left: 0;
    width: 100%;
  }
}
@keyframes move-menu-to-right-open {
  0% {
    width: 0%;
    left: -100%;
  }
  100% {
    left: 0;
    width: 100%;
  }
}

@-webkit-keyframes move-menu-to-right-close {
  0% {
    width: 100%;
    left: 0;
  }
  100% {
    left: -200%;
    width: 0%;
  }
}

@keyframes move-menu-to-right-close {
  0% {
    width: 100%;
    left: 0;
  }
  100% {
    left: -200%;
    width: 0%;
  }
}

